body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

.app{align-items:center;display:flex;justify-content:center}.center-content{text-align:center}.app{background-color:#c5d3f6;font-family:'Poppins', sans-serif}@media (min-width: 577px){.app{height:100vh;width:100vw}}@media (max-width: 576px){.app{justify-content:flex-start}}

.center-content{text-align:center}.container{display:flex;flex-direction:column;overflow:auto}.table{border-collapse:collapse;display:inline-block}@media (max-width: 576px){.container{margin-top:102px}}

.center-content{text-align:center}

.header-cell:hover{cursor:pointer}.center-content{text-align:center}.header{background-color:#6d7ae0;color:white;font-size:1.3rem}.header-cell{font-weight:300;padding:20px 35px;-webkit-user-select:none;-moz-user-select:none;-ms-user-select:none;user-select:none}@media (max-width: 576px){.header-cell{padding:5px 15px}}

.table-cell{padding:12px 25px}.center-content{text-align:center}.table-row{background-color:white;border-bottom:1px solid #f2f2f2;color:#666}.table-row:last-of-type{border-bottom:none}.table-row:hover{background-color:#ececff}.table-cell{font-size:1rem}@media (max-width: 576px){.table-row{min-height:200px}.table-cell{padding:2px 5px}}

.button:hover{cursor:pointer}.controls,.buttons{display:flex}.center-content{text-align:center}.search-and-pages .items-number,.search-and-pages .search-input{border:1px solid lightslategray;border-radius:5px;color:#666;padding:10px;transition:box-shadow 0.25s ease}.search-and-pages .items-number:focus,.search-and-pages .search-input:focus{box-shadow:0 0 10px lightslategray}.controls{background-color:white;border-top:1px solid #f2f2f2;border-bottom-left-radius:10px;border-bottom-right-radius:10px;flex-direction:column;height:100px;margin:0 auto;width:300px}.buttons{height:-webkit-fit-content;height:-moz-fit-content;height:fit-content;justify-content:space-between;margin:7px auto 0;width:90%}.button{background-color:white;box-shadow:none;border:1px solid lightslategrey;border-radius:5px;height:30px;margin:0 2px 0 0;width:45px;transition:all 0.3s ease-in}.button:hover{background-color:#6d7ae0}.button:hover .button-arrow{color:white}.button-arrow{color:lightslategrey;transition:all 0.3s ease-in}.current-page{color:#666;font-size:1.25rem}.search-and-pages{margin:12px auto 0}.search-and-pages .items-number{-webkit-appearance:none;-moz-appearance:none;appearance:none;background-image:linear-gradient(45deg, transparent 50%, lightslategrey 50%),linear-gradient(135deg, lightslategrey 50%, transparent 50%);background-position:calc(100% - 20px) calc(1.2em + 2px),calc(100% - 15px) calc(1.2em + 2px);background-size:5px 5px, 5px 5px;background-repeat:no-repeat;margin-left:5px;width:120px}.search-and-pages .search-input{width:100px}@media (max-width: 576px){.controls{border-radius:0;border-bottom:1px solid #f2f2f2;height:fit-content2px;margin:0;position:fixed;width:100vw;top:0;left:0}}

